import {Col, Row, Typography} from "antd";

type InsertProps = {
    src: string;
    alt: string;
    caption: string;
    anchor: 'left' | 'right';
    height?: string;
    width?: string;
}

const Insert = ({src, alt, caption, anchor, height, width}: InsertProps) => {
    return (<Col style={{display: 'inline-block', float: anchor, maxWidth: width, margin: '0 25px 0 25px'}}>
        <Row><img src={src} alt={alt} style={{height: height}}/></Row>
        <Row><p className="caption">{caption}</p></Row>
    </Col>);
}

function Blog() {
    return (<div>
        <Row className="navbar" align="stretch" justify="center">
            <Col xs={3}><a href="">Home</a></Col>
            <Col xs={6}/>
            <Col xs={2}><a href="#/resume">Resume</a></Col>
            <Col xs={2}><a href="#/blog">Blog</a></Col>
            <Col xs={2}><a href="#/library">Library</a></Col>
            <Col xs={2}><a href="#/projects">Projects</a></Col>
            <Col xs={3}><a href="#/connect-the-stars">Connect the Stars</a></Col>
        </Row>
        <Col xs={20} md={15} lg={12} xl={12} style={{margin: 'auto', paddingTop: '50px'}}>
            <Row justify="center">
                <h1 className="title-h1">Sensor Networks</h1>
            </Row>
            <Row justify="center">
                <h2 className="title-h2">The Smart Home, the Environment, and Industry</h2>
            </Row>
            <Row justify="center">
                <Typography>
                    <p><span className="drop-cap">W</span>with the Christmas season fast approaching, more and more
                        items are being added to wish lists. Among the toys, utensils, and gadgets, you might find
                        household
                        sensors. If you don't know what those are, you've probably already heard of them. One of the
                        most
                        recognizable sensors for your home is a smart doorbell.</p>

                    <Insert src={require('./doorbells.jpg')} anchor='right'
                            caption='Figure 1. Video doorbells are becoming very common [1]'
                            alt='Statistics about doorbell sales'
                    />

                    <p>With almost 12 million video enabled doorbells sold in 2021 alone, you've likely seen many videos
                        posted online of things that people have seen remotely<span className="footnote">[1]</span>.
                        While
                        not exactly a sensor itself, a sensor is any small gadget that collects data about its
                        surroundings
                        or state. For a video doorbell, this would be any motion that it might detect, or if the
                        doorbell
                        button has been pressed. Video doorbells are just one example of a household sensor</p>
                    <p>Various other types of sensors can also be found in households. HVAC often incorporates
                        temperature and humidity sensors to regulate indoor climate. Security systems rely on motion and
                        entry sensors as their fundamental components. In irrigation or garden systems, you'll
                        frequently
                        encounter light, water, and rain level sensors. There are other types of sensors outside of
                        these
                        systems too, such as carbon monoxide sensors and occupancy sensors.</p>
                    <p>It is important to keep in mind however, that these are still examples of sensors themselves, and
                        just having a variety of them inside your home does not mean that you have a sensor network. A
                        sensor network is a collection of sensors that are all connected to each other, and work in
                        tandem
                        for a purpose. That purpose may be just giving you information about your house environment, but
                        commonly, many different networks exist in the house at the same time. Going back to the
                        different
                        systems mentioned—HVAC, Security, and Irrigation—they can all be said to have sensor networks
                        that
                        power them.</p>
                    <p>While a smaller home may only have one or two temperature sensors powering the HVAC system, if
                        you think about the apartment complex down the street, or the dream mansion you want, this
                        number
                        expands, with all of the sensors working together to achieve a purpose. For HVAC, that's
                        informing
                        the condenser when to turn on and off, and where to direct the cold or warm air. As technology
                        improves and our lives become increasingly connected, our household systems are too. Instead of
                        many
                        different systems comprising their own sensor networks, many homes are starting to have one
                        network
                        that is comprised of all the senors, from doorbells to irrigation sensors.</p>

                    <Insert src={require('./connectivity.png')} anchor='right' height='500px' width='500px'
                            caption='Figure 2. Different Network Standards [2]'
                            alt='Statistics about IoT connectivity'
                    />

                    <h4 className="title-h4">The Smart Home</h4>
                    <p>Similar to other industries, there are many competing standards about how to connect all of these
                        sensors into a network. The most prevalent of these is wifi, likely due to the low barrier for
                        adoption as most families already have a wifi network.</p>

                    <h4 className="title-h4">The Environment</h4>
                    <h4 className="title-h4">The Dangers</h4>
                    <h4 className="title-h4">Things to keep in mind</h4>
                </Typography>
            </Row>
            <Row>
                <Typography>
                    <h3 className="title-h3">References</h3>
                    <p><span className="bold">1.</span> Narcotta, Jack. "Strategy Analytics: Amazon's Ring Remained atop
                        the Video Doorbell Market in
                        2021." Business Wire, 22 June 2023, https://www.businesswire.com/news/home/20220622005023/en/
                        Strategy-Analytics-Amazons-Ring-Remained-atop-the-Video-Doorbell-Market-in-2021.</p>
                    <p><span className="bold">2.</span> Ho, Nhu. "What Is IoT Connectivity: A Comparison Guide." EMnify
                        Blog, 28 Apr. 2023, https://www.emnify.com/blog/iot-connectivity.</p>
                    <p><span className="bold">3.</span></p>
                    <p><span className="bold">4.</span></p>
                </Typography>
            </Row>
        </Col>
    </div>);
}

export default Blog;